/* App.css */

.container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #f4f4f4;
}

.editor-container,
.preview-container {
  width: 50%;
  padding: 20px;
}

textarea {
  width: 100%;
  height: 400px;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

#preview {
  padding: 20px;
  font-size: 16px;
  line-height: 1.6;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }
  
  .editor-container,
  .preview-container {
    width: 100%;
  }
}

/* App.css */

.container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.light {
  background-color: #f4f4f4;
  color: #333;
}

.dark {
  background-color: #333;
}

.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}